.site-footer {
    background: #ad8734;
    color: #dbe8ff;
    padding: 28px 16px;
    margin-top: 20px;
}

.footer-inner {
    max-width: 1100px;
    margin: 0 auto;
    display: flex;
    gap: 24px;
    align-items: flex-start;
    justify-content: space-between
}

.site-footer h4 {
    margin: 0 0 8px
}

.site-footer p,
.site-footer a,
.site-footer li {
    color: inherit
}

.site-footer a {
    text-decoration: none
}

.site-footer ul {
    list-style: none;
    padding: 0;
    margin: 0
}

.site-footer .col {
    flex: 1;
    min-width: 180px
}

.footer-bottom {
    max-width: 1100px;
    margin: 16px auto 0;
    padding-top: 12px;
    border-top: 1px solid rgba(255, 255, 255, 0.04);
    text-align: center
}

@media (max-width:700px) {
    .footer-inner {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 12px
    }

    .col {
        justify-content: center;
    }
}